<template>
<div class="my-popups">
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        {{ $t('Request For Return') }}
                    </h5>

                    <button v-on:click="CloseRequestForReturnProduct($event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>

                </div>

                <div class="modal-body">
                    <div class="listing">
                        <div class="table-responsive table-fix-header" id="returnrecordBulk">
                            <table id="tblReturnRequestBulk" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">
                                    <tr>

                                        <th><span>{{ $t('ProductName') }}</span></th>
                                        <th><span>{{ $t('UniqueName') }} </span></th>
                                        <th><span>{{ $t('LocationName') }} </span></th>
                                        <th><span>{{ $t('IssuedTo') }} </span></th>
                                        <th><span>{{ $t('IssuedBy') }} </span></th>
                                        <th><span>{{ $t('AssignedFrom') }}</span></th>
                                        <th><span>{{ $t('AssignedTill') }} </span></th>
                                        <th><span>{{ $t('NumberofProducts') }} </span></th>
                                        <th scope="col" class="text-center"><span>{{ $t('Quantity') }} </span></th>
                                        <th><span>{{ $t('ReturnUntil') }}</span></th>
                                    </tr>
                                </thead>

                                <tbody v-for="(item, index) in requestForReturnData" :key="index">
                                    <tr>
                                        <td>{{ item.AssetName }}</td>
                                        <td>{{ item.UniqueName }}</td>
                                        <td>{{ item.LocationName }}</td>
                                        <td>{{ item.IssuedToName }}</td>
                                        <td>{{ item.IssuedByName }}</td>
                                        <td>{{ item.AssignedFrom }}</td>
                                        <td>{{ item.AssignedTill }}</td>
                                        <td class="text-center">{{ item.NumberOfItemsToAssign }}</td>

                                        <td >
                                            <input v-if="item.CreateUniqueNameListing" type="number" class="form-control"  v-model="item.NumberOfItemsToAssign" maxlength="5" disabled="disabled" />

                                            
                                            <input type="decimal" :min="1" :id="'returen_'+index"  v-on:keypress="preventMinusSign($event)"  v-on:keyup="checkQuantity(item,item.Quantity,index)" maxlength="5" :class="{ 'form-control': true, 'is-invalid': Quantity == 0 ? true : false }" v-model="item.Quantity" />

                                        </td>

                                        <td>
                                            <v-date-picker :popover="popover" v-bind:name="item.ReturnUntill" :min-date="getMinDate()" mode="date" :minute-increment="5" class="timedate  input-group" v-model="item.ReturnUntill">
                                                <template v-slot="{ inputValue, togglePopover }">
                                                    <input placeholder="M/D/YYYY h:mm A" on-select="close" @click="togglePopover()" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                    <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                </template>
                                            </v-date-picker>

                                        </td>

                                    </tr>
                                    <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">
                                        {{ $t('NoRecordfound') }}</div>

                                </tbody>

                            </table>

                        </div>
                    </div>

                    <div id="dvReturnStatus" class="form-content mt-2">
                        <div class="collapse show py-3">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="form-group">
                                    <label>{{ $t('Comment') }}</label>
                                    <textarea name="txtComment" id="txtComment" class="form-control" v-model="comment" maxlength="200"></textarea>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="form-group">
                                    <div class="search-btm-btn">
                                        <a class="btn btn-success formbtn text-white m-2" @click="SubmitData()" title="Save" id="btnBulkRequestForReturnItem"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</a>
                                        <a class="btn btn-danger formbtn text-white" title="Cancel" v-on:click="CloseRequestForReturnProduct($event)"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService';
import { DateTime } from 'luxon';

export default {
    props: {
        assDetailIDs: {
            type: String,
            required: true
        },
        newValue: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            noRecordForTab: false,
            itemdetails: "",
            comment: "",
            datebind: "",
            selectedDate: new Date(),
            requestForReturnData: [],
            tempArray: [],
            popover: {
                visibility: 'click'
            },
            Quantity: 1,
            Timezone:''

        }
    },

    created: function () {

        this.GetAssignmentDetailByID(this.newValue);
        this.UserId = this.GetUserInfo.ID;
        this.Timezone = this.GetUserInfo.TimeZone;

    },
    methods: {
        preventMinusSign(event) {
        if (event.key === '-' || event.key === '.' || event.key === '+' || event.key === '0') {
            event.preventDefault();
        }
    },
        
        async GetAssignmentDetailByID(event) {
            var url = `assDetailIDs=${event}`
            await DataService.GetAssignmentDetailByID(url).then(response => {
                if (response.data != null) {
                    this.requestForReturnData = response.data;
                    this.requestForReturnData.map((ele => {
                        ele.Quantity = this.Quantity;
                        ele.ReturnUntill = new Date();
                    }))
                    this.noRecordForTab = false;
                } else {
                    this.noRecordForTab = true;
                }
            });
        },

        CloseRequestForReturnProduct: function () {
            this.$parent.CloseRequestForReturnProduct();
        },       

        
        checkQuantity: function (item, Quantity, indx) {            
            var vm = this;
            let TotalQuantity = item.NumberOfItemsToAssign
            let currentValue = Quantity;
            if (item.Quantity != '') {
                vm.requestForReturnData.forEach((itm, ind) => {
                    if (ind == indx) {
                        if (parseInt(currentValue) > TotalQuantity) {
                            vm.ShowAlert(vm.$t(`Please enter a value less than or equal to ${TotalQuantity}`), "warning", vm.$t("Alert"));
                            vm.requestForReturnData[indx].Quantity = TotalQuantity;
                            vm.$forceUpdate()
                        }
                        else {
                            vm.requestForReturnData[indx].Quantity = Quantity;
                            vm.$forceUpdate()
                        }
                    }
                })
            }
        },
        SubmitData: function () {            
            var vm = this;
            let detail = "",
                realtedinfoIDs = "",
                status = true

            var isCollectedHighlightedError = false;

            vm.requestForReturnData.forEach(ele => {

                if (ele.Quantity == "" || ele.Quantity == 0) {
                    isCollectedHighlightedError = true;
                }               
                vm.qty = parseInt(ele.Quantity);
                vm.assDetailID = ele.AssignmentDetailId;
                vm.userid = ele.IssuedToId;
                vm.RelatedInfoID = ele.RelatedInfoID;
                vm.NumberOfItemsToAssign = ele.NumberOfItemsToAssign;
                vm.UniqueName = ele.UniqueName;
                vm.requestUntill = vm.$options.filters.formatDateTimeYYMMDD(ele.ReturnUntill);
                if (detail != "") detail += "|";
                detail += vm.assDetailID + "," + vm.qty + "," + vm.userid;

                if (realtedinfoIDs != "") realtedinfoIDs += "|";
                realtedinfoIDs += vm.assDetailID + "," + vm.RelatedInfoID + "," + vm.qty + "," + this.UserId + "," + (vm.requestUntill);

            });

            if (isCollectedHighlightedError == true) {
                return vm.ShowAlert(
                    vm.$t('Quantity is required'),
                    "warning",
                    vm.$t("Alert")
                );

            }

            if (detail == "") {
                vm.ShowAlert(vm.$t("Minimum One Product should be selected."), "warning", vm.$t('Alert'));
                return false;
            }

            if (status == false) {
                return false;
            }

            var data = `itemdetails=${realtedinfoIDs}&comment=${vm.comment}`;
            DataService.BulkRequestForReturn(data).then(response => {
                if (response.data == 'False') {
                    vm.ShowAlert(vm.$t("Request for Return saved successfully."), "success", vm.$t('Alert'));
                    vm.$parent.FetchData();
                    vm.CloseRequestForReturnProduct();

                } else {
                    vm.ShowAlert(vm.$t("Error while returning item."), "failure", vm.$t('Alert'));
                    vm.$parent.FetchData();
                    vm.CloseRequestForReturnProduct();

                }

            });

        },

    }
}
</script>

<style scoped>
.listing {
    float: none;
    width: 100%;
    position: relative;
}
#returnrecordBulk{
    overflow: visible;
}
#returnrecordBulk .table tr td {
    overflow: visible !important;
}
</style>
